import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"

const IndexPage = () => (
  <Layout>
    <SEO title="Home" />
    <article>
        <img className={"person_photo"} src={'../../tribushnyaya/tribushnyaya.jpg'} alt={""} />
        <div>
          <div className={"person_name"}>Галина Трибушняя</div>
        </div>
      <div className={"textBlock"}>
        <p>Если честно, то&nbsp;я&nbsp;думала, что будет гораздо сложнее сидеть дома. Пришлось отказаться от&nbsp;многих привычных вещей, осваивать непривычную форму работы, но, с&nbsp;другой стороны, появилось больше время для своих хобби и&nbsp;новых начинаний. Труднее всего было отказаться от&nbsp;занятий волейболом, пришлось искать альтернативу, поэтому мы&nbsp;с&nbsp;братом организовали онлайн тренировки для поддержания формы.</p>
        <p>Я&nbsp;стараюсь всегда проводить время продуктивно, и&nbsp;самоизоляция не&nbsp;стала исключением. Например, я&nbsp;начала изучать работу в&nbsp;photoshop, так как давно увлекаюсь фотографией, а&nbsp;также вспомнила уроки рисования, хотя не&nbsp;брала кисть в&nbsp;руки уже около года.</p>
        <p>Чтение&nbsp;&mdash; одно из&nbsp;моих любимых времяпровождений, за&nbsp;этот период времени успела прочитать пока только две книги: Теодор Драйзер &laquo;Финансист&raquo; и&nbsp;Курт Воннегут &laquo;Бойня&#8470;&nbsp;5&raquo;. Обе книги мне очень понравились, и&nbsp;я&nbsp;рекомендую их&nbsp;к&nbsp;прочтению.</p>
        <p>У&nbsp;нас очень дружная семья, и&nbsp;одна из&nbsp;традиций&nbsp;&mdash; это вечерние совместные посиделки, когда мы&nbsp;смотрим вместе фильмы, играем в&nbsp;настольные игры, общаемся, и&nbsp;сейчас эти традиции еще крепче закрепились в&nbsp;нашей семье.</p>
        <p>Современные проблемы, требуют современных решений, и&nbsp;поэтому наши встречи с&nbsp;друзьями теперь проходят онлайн, и&nbsp;это очень даже здорово. Конечно, они не&nbsp;заменят живых встреч, но&nbsp;это тоже бывает очень весело и&nbsp;познавательно. В&nbsp;таком формате нам удалось даже отметить день рождения подруги, было забавно.</p>
        <p>Когда закончится изоляция, наверное, я&nbsp;отправлюсь в&nbsp;свой любимый парк &laquo;Лосиный остров&raquo; на&nbsp;велосипеде, чтобы насладиться природой, пением птиц в&nbsp;полной мере, мне этого очень не&nbsp;хватает.</p>
        <p>Мы&nbsp;уже сейчас можем видеть, что пандемия не&nbsp;пройдет для нас бесследно. Мне кажется, уже произошла переоценка многих ценностей. Очень жаль, что многие сейчас несут большие потери, закрывается малый бизнес, люди остаются без работы, в&nbsp;том числе, к&nbsp;сожалению, и&nbsp;мои знакомые. Будем надеяться, что все это скоро закончится. Положительным моментом является то, что мы&nbsp;теперь <nobr>наконец-то</nobr> можем по&nbsp;достоинству оценить работу жизненно важных профессий.</p>      </div>
    </article>
  </Layout>
)

export default IndexPage



